<template>
  <div class="poseidon-game" :class="{'opened': isOpen}" >
    <div class="head-container">
      <img class="poseidon-tape" src="../../assets/images/various/tape.png" alt="">
      <div class="inner-head-container">
        <div class="head"  @mousedown="isOpen = !isOpen" >
          <div class="eyes">
            <div class='left eye'></div>
            <div class='right eye'></div>
          </div>
          <img draggable="false" class="head" src="../../assets/images/poseidon/poseidon_w_mouth.png" alt="">
          <img draggable="false" class="mouth" src="../../assets/images/poseidon/poseidon_mouth.png" alt="">
          <img draggable="false" class="oyster" src="../../assets/images/poseidon/oyster.png" alt="">
        </div>
      </div>
      <h3>Titillez-moi la moustache</h3>
    </div>
    <div class="poseidon-letter">
      <p>Pour <simple-data-counter :display-inline="true" :use-currency-font="useCurrencyFont" :use-sinatra-font="useSinatraFont" :percent="percents.aquaticSpit"></simple-data-counter> des lves,
      une hutre est un simple mollard aquatique</p>
      <div class="love" :class="{'hidden': !isOpen, 'visible': isOpen}">
        <img src="../../assets/images/various/papier_huitre.png" alt="">
        <div class="response">
          <transition name="fade">
            <p v-if="isOpen">Pour les <simple-data-counter :display-inline="true" :use-currency-font="useCurrencyFont" :use-sinatra-font="useSinatraFont" :percent="percents.poseidonInvention"></simple-data-counter> restant,
           il s'agit d'une invention divine de posidon.</p>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { EventBus } from '../../main'
  import Utils from '../../Utils'
  import DataMapper from '../../utils/DataMapper'
  import SimpleDataCounter from '../SimpleDataCounter.vue'
  export default {
    components: {SimpleDataCounter},
    name: 'poseidon-game',
    props: {
      answer: {type: Object, required: true},
      useCurrencyFont: {type: Boolean, default: false},
      useSinatraFont: {type: Boolean, default: false}
    },
    data () {
      return {
        isOpen: false
      }
    },
    mounted () {
      EventBus.$on('poseidon-move', (e) => {
        this.onMouseMove(e)
      })
    },
    computed: {
      percents () {
        return DataMapper.getAverageForAnswer(this.answer)
      }
    },
    methods: {
      onMouseMove (e) {
        let eyesContainer = this.$el.querySelector('.eyes')
        let eyes = this.$el.querySelectorAll('.eye')
        let x = e.pageX
        let y = e.pageY
        let offset = Utils.getCoords(eyesContainer)
        let left = (offset.left - x)
        let top = (offset.top - y)
        let rad = -(Math.atan2(top, -left))
        eyes.forEach(eye => {
          eye.style.transform = 'rotate(' + rad + 'rad)'
        })
      }
    }
  }
</script>

<style lang="scss">
  @import "../../assets/styles/utils/variables";

  .poseidon-game {
    position: relative;
    text-align: center;
    transform: rotate(-3deg) scale(0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 350px;
    & .poseidon-letter {
      background: url('../../assets/images/various/papier_huitre2.png');
      transform: rotate(5deg);
      background-size: cover;
      width: 1062px;
      height: 500px;
      justify-content: space-around;
      align-items: center;
      padding: 40px;
      display: flex;
      flex-direction: column;
      & > p {
        max-width: 600px;
      }
      & .love {
        position: relative;
        width: 100%;
        height: 130px;
        text-align: center;
        & .response {
          position: absolute;
          z-index: 1;
          bottom: 30px;
          max-width: 600px;
          left: calc(50% - 300px);
        }
        & img {
          transition: all 0.3s ease-in;
          position: absolute;
          z-index: 2;
          left: 0;
          top: -20px;
        }
        &.visible {
          & img {
            transform: translateX(900px);
          }
        }
      }
    }
    & p {
      margin-top: 50px;
      font-size: 2.1rem;
      text-align: center;
      text-transform: uppercase;
    }
    & h3 {
      font-family: "Mister Sinatra";
      text-transform: uppercase;
      text-align: center;
      padding-top: 40px;
      margin: 0 40px;
      /*letter-spacing: 0.7rem;*/
      font-size: 2.1rem;
    }
    & > *:first-child {
      margin-right: 60px;
      text-align: right;
    }
    & > *:last-child {
      margin-left: 60px;
      text-align: left;
    }
    & .head-container {
      padding: 60px;
      padding-bottom: 40px;
      background-size: 552px;
      background: url('../../assets/images/poseidon/texture_bg.png') repeat;
      background-size: 500px;
      box-shadow: $shadow;
      position: relative;
      width: 654px;
      & .poseidon-tape {
        position: absolute;
        top: -100px;
        left: -100px;
        transform: rotate(90deg);
      }
      & .inner-head-container {
        padding: 40px;
        padding-bottom: 60px;
        background: #d0d0d0;
      }
      & .head {
        user-select: none;
        position: relative;
        margin: 0 auto;
        width: 400px;
        z-index: 100;
      }
      & .mouth {
        transition: all 0.3s;
        width: 400px;
        position: absolute;
        top: 0;
        z-index: 98;
        left: 0;
      }
      & .oyster {
        transition: all 0.3s;
        position: absolute;
        transform: rotate(90deg);
        left: calc(50% - 46px);
        z-index: 99;
        bottom: calc(25% + 110px);
      }
    }
    &.opened {
      & .mouth {
        top: 50px;
      }
      & .oyster {
        bottom: 23%;
      }
    }
    & .eyes {
      display: flex;
      justify-content: space-between;
      width: 200px;
      left: calc(50% - 100px);
      top: 170px;
      z-index: 100;
      position: absolute;
      & .eye {
        position: relative;
        display: inline-block;
        border-radius: 50%;
        height: 50px;
        width: 50px;
        background: url('../../assets/images/poseidon/oeilposeidon.png') center center / cover;
        &.left {
          left: 26px;
          top: 1px;
        }
        &.right {
          top: 5px;
          right: 15px;
        }
      }
      & .eye:after {
        position: absolute;
        bottom: 17px;
        right: 10px;
        width: 15px;
        height: 15px;
        background: #000;
        filter: blur(1px);
        border-radius: 50%;
        content: " ";
      }
    }
  }
</style>



