<template>
  <div class="sticker-data-counter hidden" :class="color">
    <div>
      <span ref="percent" :class="classString">{{ percent }}</span><span :class="'percentage ' + size">%</span>
    </div>
  </div>
</template>

<script>
  import Utils from '../../Utils'

  export default {
    name: 'sticker-data-counter',
    props: {
      percent: {type: Number, required: true},
      size: {type: String, default: 'medium'},
      color: {type: String, default: 'red'},
      duration: {type: Number, default: 3}
    },
    mounted () {
      let options = {
        root: null,
        threshold: 1
      }
      this.observer = new IntersectionObserver(this.observerCallback.bind(this), options)
      this.observer.observe(this.$el)
    },
    methods: {
      observerCallback (entries, observer) {
        entries.forEach(entry => {
          if (entry.intersectionRatio > 0) {
            Utils.animateValue(this.$refs.percent, 0, this.percent, 0, this.duration * 2)
            this.$el.classList.remove('hidden')
            observer.unobserve(this.$el)
          }
        })
      }
    },
    data () {
      return {}
    },
    computed: {
      classString () {
        let str = `percent ${this.size}`
        str += this.useFrontageFont ? ' custom-font' : ''
        return str
      }
    }
  }
</script>

<style lang="scss">
  @import "../../assets/styles/utils/variables";

  .sticker-data-counter {
    background-size: contain;
    width: 133px;
    height: 129px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: scale(0.7);
    transition: opacity 1s, filter 0.7s, transform 1.3s;
    &.hidden {
      visibility: hidden;
      opacity: 0;
      transform: scale(0.7) translateX(-20px);
      filter: blur(5px)
    }
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: url('../../assets/images/gazette/autocollant.png');
      background-size: contain;
      transform: rotate(45deg);
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.08);
    }
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      transform: rotate(45deg) scale(0.8);
      background: transparent;
      border: 2px solid $red;
      z-index: 1;
    }
    &.red:after {
      border: 2px solid $red;
    }
    &.blue:after {
      border: 2px solid $blue;
    }
    &.green:after {
      border: 2px solid $green;
    }
    & > * {
      position: relative;
      z-index: 1;
    }
    &.red {
      color: $red
    }
    &.blue {
      color: $blue;
    }
    &.green {
      color: $green;
    }
    & .explanation {
      font-family: $font-family;
    }
    & .percent {
      font-family: 'Mister Sinatra Slim';
      &.small {
        font-size: 1.5rem;
      }
      &.medium {
        font-size: 3rem;
      }
      &.large {
        font-size: 5rem;
      }
    }
    & .percentage {
      &.small {
        font-size: 0.7rem;
      }
      &.medium {
        font-size: 1.5rem;
      }
      &.large {
        font-size: 2rem;
      }
    }
  }
</style>



