<template>
  <div id="app" @mousemove="onMouseMove">
    <div class="container">
      <div class="row title">
        <img class="fourchette" src="./assets/images/various/fourchette.png" alt="">
        <img class="logo" src="./assets/images/various/datable.png" alt="Datable">
        <img class="subtitle" src="./assets/images/various/sous_titre.png" alt="Dans l'estomac d'un BDDI">
        <img class="couteau" src="./assets/images/various/couteau.png" alt="">
      </div>
      <div class="row">
        <poseidon-game :use-sinatra-font="true" :answer="getAnswer('oysterPreferences')"></poseidon-game>
      </div>
      <div class="row full-width small flex-end spaced-margin relative">
        <gazette-bddi :answer="getAnswer('cerealPreferences')"></gazette-bddi>
        <img class="aside lolipop" src="./assets/images/various/lollipop_shadow.png" alt="">
      </div>
      <div class="row spaced-margin eggs-row relative">
        <game-scratcher :use-currency-font="true" :rotation="-4" :background="ticketProt" :topImg="egg" :bottomImg="eggProteines"
        :percent="getAverageFor('eggPreferences').proteinesAdd"></game-scratcher>
        <game-scratcher :use-currency-font="true" :rotation="2" :background="ticketPioupiou" :topImg="egg" :bottomImg="eggPoussin"
        :percent="getAverageFor('eggPreferences').poussinBecome"></game-scratcher>
        <img class="aside paille" src="./assets/images/various/paille.png" alt="">
      </div>
      <div class="row ">
        <tinder-game class="spaced-margin extra-large" :cards="cards"></tinder-game>
        <addition></addition>
      </div>
          <!--<pizza-chart :use-currency-font="true" :answer="getAnswer('pizzaPreferences')"></pizza-chart>-->
          <!--<chocolate-question :answer="getAnswer('chocolateBread')"></chocolate-question>-->
      <licorice-game :answer="licorice"></licorice-game>
    </div>
  </div>
</template>

<script>
  import Navigation from './components/Navigation.vue'
  import SimpleDataCounter from './components/SimpleDataCounter.vue'
  import GameScratcher from './components/GameScratcher.vue'
  import DataMapper from './utils/DataMapper'
  import PizzaChart from './components/PizzaChart.vue'
  import egg from './assets/images/egg.png'
  import eggPoussin from './assets/images/eggPoussin.png'
  import poseidonPicture from './assets/images/poseidon_picture.jpg'
  import eggProteines from './assets/images/eggProteines.png'
  import ticketPioupiou from './assets/textures/ticket_pioupiou_vide.png'
  import ticketProt from './assets/textures/ticket_prot_vide.png'
  import LicoriceGame from './components/LicoriceGame.vue'
  import Addition from './components/Addition.vue'
  import ChocolateQuestion from './components/ChocolateQuestion.vue'
  import GazetteBddi from './components/GazetteBDDI.vue'
  import PoseidonGame from './components/Poseidon/PoseidonGame.vue'
  import { EventBus } from './main'
  import TinderGame from './components/TinderGame.vue'
  import reglisseImg from './assets/images/licorice_tinder/licorice.png'
  import roquefortImg from './assets/images/licorice_tinder/roquefort.png'
  import pizzaImg from './assets/images/licorice_tinder/pizza.png'
  import MenuCard from './components/MenuCard.vue'

  export default {
    name: 'app',
    components: {
      MenuCard,
      TinderGame,
      PoseidonGame,
      GazetteBddi,
      ChocolateQuestion,
      Addition,
      LicoriceGame,
      PizzaChart,
      GameScratcher,
      SimpleDataCounter,
      Navigation
    },
    data () {
      return {
        answers: DataMapper.getData(),
        cards: [
          {id: 'licoricePreferences', img: reglisseImg, name: 'Rgisse, 22 ans', desc: 'Friandises  mi-temps'},
          {id: 'roquefortPreferences', img: roquefortImg, name: 'The rockfort, 45 ans', desc: 'Ex catcheur  la WWE'},
          {id: 'pizzaPreferences', img: pizzaImg, name: 'Pizza'}],
        egg,
        eggPoussin,
        eggProteines,
        ticketProt,
        ticketPioupiou,
        licorice: DataMapper.getAnswer('licoricePreferences'),
        poseidonPicture,
        reglisseImg,
        pizzaImg,
        roquefortImg
      }
    },
    methods: {
      getAnswer (questionKey) {
        return DataMapper.getAnswer(questionKey)
      },
      getAverageFor (questionKey) {
        return DataMapper.getAverageFor(questionKey)
      },
      onMouseMove (e) {
        EventBus.$emit('poseidon-move', e)
      }
    },
    mounted () {
      document.querySelectorAll('.aside-picture').forEach(i => {
        i.setAttribute('draggable', false)
      })
      EventBus.$on('card-removed', card => {
        console.log(card)
        let last = this.cards.shift()
        this.cards.push(last)
        if (this.cards[0].id === 'licoricePreferences') {
          EventBus.$emit('cards-reseted')
        }
      })
    }
  }
</script>

<style lang="scss">
  @import "./assets/styles/utils/variables";

  #app {
    font-family: $font-family;
    letter-spacing: 0.2rem;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: $main-font-color;
    margin-top: 200px;
    position: relative;
    & .aside {
      position: absolute;
      top: 0;
      left: 0;
      &.lolipop {
        left: -200px;
        transform: rotate(32deg);
      }
      &.ciseaux {
        transform: scale(0.8) rotate(52deg);
        top: 900px;
        left: -500px;
        z-index: 1000;
      }
      &.paille {
        z-index: 1000;
        top: -500px;
        left: calc(100% + 500px);
        transform: rotate(72deg)
      }
    }
  }
</style>



