<template>
    <div class="chocolate-question">
      <h2>Pain au chocolat <span ref="chocolatine">ou chocolatine</span> ?</h2>
      <simple-data-counter :percent="percents.chocolate"></simple-data-counter>
      <p>des tudiants ont rpondu <span class="underline">pain au chocolat</span> <small>(comment a y avait pas d'autres choix ?)</small></p>
    </div>
</template>

<script>
  import DataMapper from '../utils/DataMapper'
  import SimpleDataCounter from './SimpleDataCounter.vue'

  export default {
    components: {SimpleDataCounter},
    name: 'chocolate-question',
    props: {
      answer: {type: Object, required: true}
    },
    mounted () {
      let options = {
        root: null,
        threshold: 0.2
      }
      this.observer = new IntersectionObserver(this.observerCallback.bind(this), options)
      this.observer.observe(this.$el)
    },
    computed: {
      percents () {
        return DataMapper.getAverageForAnswer(this.answer)
      }
    },
    methods: {
      observerCallback (entries, observer) {
        entries.forEach(entry => {
          if (entry.intersectionRatio > 0) {
            this.$refs.chocolatine.classList.add('striked')
            observer.unobserve(this.$el)
          }
        })
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/styles/utils/variables";

  .chocolate-question {
    & span {
      transition: all 0.3s;
      &.striked {
        position: relative;
        &:before {
          animation: scratchAnimation 0.5s cubic-bezier(0.47, -0.37, 0.54, 1.38) forwards;
          animation-delay: 1s;
          clip-path: inset(0 100% 0 0);
          content: '';
          position: absolute;
          height: 6px;
          background: $main-font-color;
          top: calc(50% - 2px);
          transform: rotate(-5deg);
          left: 0;
          width: 100%;
        }
        &:after {
          animation: scratchAnimationReverse 0.5s cubic-bezier(0.47, -0.37, 0.54, 1.38) forwards;
          animation-delay: 1.7s;
          clip-path: inset(0 0 0 100%);
          content: '';
          position: absolute;
          height: 6px;
          background: $main-font-color;
          transform: rotate(5deg);
          top: calc(50% - 2px);
          left: 0;
          width: 100%;
        }
      }
    }
  }

  @keyframes scratchAnimation {
    0% {
      clip-path: inset(0 100% 0 0);
    }
    100% {
      clip-path: inset(0 0 0 0);
    }
  }

  @keyframes scratchAnimationReverse {
    0% {
      clip-path: inset(0 0 0 100%);
    }
    100% {
      clip-path: inset(0 0 0 0);
    }
  }
</style>



