<template>
    <div class="gazette-bddi">
      <img class="aside ciseaux" src="../assets/images/various/ciseaux.png" alt="">
      <img src="../assets/images/gazette/curieux.jpg" alt="">
      <div class="main-title">
        <h2>La gazette des Bddi</h2>
        <img class="tampon" src="../assets/images/gazette/tampon.png" alt="">
      </div>
      <div class="content">
        <div class="title">
          <img src="../assets/images/gazette/lait.png" alt="">
          <div class="description">
            <h2>L'&oelig;uf ou la poule ? <br/>Le lait ou les crales ?</h2>
            <p>Enqute saisissante au coeur d'une classe divise</p>
          </div>
          <img src="../assets/images/gazette/oeuf.png" alt="">
        </div>
        <div class="answers">
          <div class="answer">
            <sticker-data-counter :percent="percents.beforeMilk"></sticker-data-counter>
            <div class="answer-content">
              <img src="../assets/images/gazette/bol.png" alt="">
              <p>Mettent les crales <span class="underline">avant</span> le lait<br/> <span class="sub">(encore heureux)</span></p>
            </div>
          </div>
          <div class="answer">
            <sticker-data-counter color="blue" :percent="percents.afterMilk"></sticker-data-counter>
            <div class="answer-content">
              <img src="../assets/images/gazette/bol2.png" alt="">
              <p>Mettent les crales <span class="underline">aprs</span> le lait<br/> <span class="sub">(tout a pour se dmarquer)</span></p>
            </div>
          </div>
          <div class="answer">
            <sticker-data-counter color="green" :percent="percents.lactoseIntolerant"></sticker-data-counter>
            <div class="answer-content">
              <img src="../assets/images/gazette/bol3.png" alt="">
              <p>Sont intolrants au lactose<br/> <span class="sub">(pas de lait, pas de crales)</span></p>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import StickerDataCounter from './Gazette/StickerDateCounter.vue'
  import DataMapper from '../utils/DataMapper'

  export default {
    components: {StickerDataCounter},
    name: 'gazette-bddi',
    props: {
      answer: {type: Object, required: true}
    },
    computed: {
      percents () {
        return DataMapper.getAverageForAnswer(this.answer)
      }
    }
  }
</script>

<style lang="scss">
  .gazette-bddi {
    display: flex;
    transform: scale(0.9) rotate(5deg);
    flex-direction: column;
    background: url('../assets/images/gazette/pliure.png');
    width: 882px;
    height: 1303px;
    padding: 40px 40px 40px 70px;
    & header table {
      border-collapse: collapse;
      & td {
        padding: 10px;
        border: 1px solid black;
      }
      & tr > td:nth-of-type(2) {
        width: 90%;
      }
    }
    & .main-title {
      padding: 20px 0;
      border-bottom: 2px solid black;
      margin-bottom: 20px;
      position: relative;
      font-family: 'Old English Text MT';
      position: relative;
      & .tampon {
        position: absolute;
        width: 120px;
        right: 10px;
        top: 70px;
        transform: rotate(25deg);

      }
      &:after {
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        background: black;
        left: 0;
        bottom: -4px;
      }
      & h2 {
        letter-spacing: 0.5rem;
        text-align: center;
        font-size: 4.9rem;
      }
    }
    & .content {
      padding: 20px;
      padding-bottom: 0;
      border: 2px black solid;
      mix-blend-mode: multiply;
      & .title {
        display: flex;
        text-align: center;
        font-family: "Currency";
        justify-content: space-between;
        & .description {
          display: flex;
          flex-direction: column;
          justify-content: center;
        }
        & img {
          height: 120px;
        }
        & h2 {
          font-size: 1.7rem;
        }
        & p {
          font-family: 'Mister Sinatra Slim';
          margin-top: 10px;
          font-size: 1.3rem;
          letter-spacing: 2px;
        }
      }
      & .answers {
        display: flex;
        flex-direction: column;
        padding: 20px;
        & .answer {
          display: flex;
          flex-direction: row;
          & .sticker-data-counter {
            margin-top: 20px;
          }
          & .answer-content {
            display: flex;
            flex: 1;
            flex-direction: column;
            text-align: center;
            align-items: center;
            & img {
              width: 400px;
            }
            & p {
              font-family: "Mister Sinatra Slim";
              letter-spacing: 1px;
              font-size: 1.3rem;
              text-transform: uppercase;
              margin-top: 20px;
            }
          }
          margin-bottom: 20px;
          margin-top: 20px;
          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }
    }
  }
</style>



