<template>
    <div class="menu-card">
      <h2 class="handed-style">Entres</h2>
      <div class="row center-xs">
        <h2 class="align-center">Plutt sucr ou sal ?</h2>
      </div>
      <div class="row double">
        <div class="col-xs align-right">
          <simple-data-counter :percent="sweetAnswer.salty"> des lves prfrent le sucr</simple-data-counter>
        </div>
        <div class="col-xs">
          <simple-data-counter :percent="sweetAnswer.sweet"> des lves
            prfrent le sal</simple-data-counter>
        </div>
      </div>
      <div class="row center-xs top-border">
        <div class="col-xs center-xs padding-top">
          <simple-data-counter class="align-center" :percent="sweetAnswer.both"> des lves
            prfrent le sucr</simple-data-counter>
        </div>
      </div>
    </div>
</template>

<script>
  import SimpleDataCounter from './SimpleDataCounter.vue'
  import DataMapper from '../utils/DataMapper'

  export default {
    components: {SimpleDataCounter},
    name: 'menu-card',
    data () {
      return {
        sweetAnswer: DataMapper.getAverageFor('sweetOrSalty'),
        chocolateAnswer: DataMapper.getAverageFor('chocolateBread')
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/styles/utils/variables";

  .menu-card {
    transform: rotate(-4deg);
    background: url('../assets/textures/paper.png') repeat;
    clip-path: polygon(1% 0.5%, 88% 0%, 91% 0%, 98% 1%, 100% 13%, 100% 17%, 100% 20%, 100% 26%, 100% 34%, 100% 43%, 100% 49%, 100% 57%, 100% 63%, 99% 69%, 100% 84%, 100% 93%, 100% 99%, 98% 100%, 92% 100%, 84% 100%, 73% 100%, 62% 100%, 56% 100%, 45% 100%, 38% 100%, 24% 99%, 16% 100%, 8% 100%, 1% 100%, 0% 91%, 1% 84%, 0% 76%, 0% 67%, 0% 51%, 0% 41%, 2% 2%, 5% 8%, 3% 7%, 0% 3%);
    box-shadow: $shadow;
    filter: drop-shadow($shadow);
    padding: 40px;
    position: relative;
    width: 800px;
    font-size: 1.5rem;
    & .simple-data-counter {
      margin-bottom: 20px;
      text-transform: uppercase;
    }
    & .row.double {
      & > *:first-of-type {
        padding-right: 40px;
        border-right: 2px solid black;
      }
      & > *:last-of-type {
        padding-left: 40px;
        border-left: 2px solid black;
      }
    }
    & h2 {
      margin: 20px 20px 40px 20px;
    }
    & .top-border {
      border-top: 4px solid black;
    }
    & .margin-top {
      margin-top: 20px;
    }
    & .padding-top {
      margin-top: 20px;
    }
    & .handed-style {
      font-family: 'Old English Text MT';
      font-size: 4rem;
      position: relative;
      border-bottom: 2px solid black;
      width: 50%;
      text-align: center;
      margin: 0 auto 20px auto;

      &:after {
        position: absolute;
        content: '';
        width: 100%;
        height: 3px;
        background: black;
        left: 0;
        bottom: -8px;
      }
    }
  }
</style>



