<template>
  <div class="tinder-game">
    <div class="iphone">
      <div class="iphone-content">
        <div class="tinder-app" :class="{'overlay-visible': lovedResponse || hatedResponse}">
          <transition name="fadeGrowCenter" mode="out-in">
            <div v-if="lovedResponse" class="tinder-match-overlay">
              <h2>It's a match</h2>
              <div>
                <simple-data-counter size="large" class="align-center" :percent="percents['love']"></simple-data-counter>
                <p>
                  {{ responses['love'] }}
                </p>
              </div>
              <button @click="closeOverlay">OK</button>
            </div>
          </transition>
          <transition name="fadeGrowCenter" mode="out-in">
            <div v-if="hatedResponse" class="tinder-match-overlay">
              <h2>It's not a match</h2>
              <div>
              <simple-data-counter size="large" class="align-center" :percent="percents['hate']"></simple-data-counter>
                <p>
                  {{ responses['hate'] }}
                </p>
              </div>

              <button @click="closeOverlay">OK</button>
            </div>
          </transition>
          <div class="app-bar">
            <img class="app-icon" draggable="false" src="../assets/images/licorice_tinder/logoeater.png" alt="">
          </div>
          <div class="app-content">
            <div class="tinder-cards">
              <div v-for="(card, i) in cards" class="tinder-card" :key="card.id" :class="{'current': currentCard.id === card.id}">
                <img draggable="false" :src="card.img" alt="">
                <div class="informations"><h3>{{ card.name }}</h3>
                  <p v-if="card.desc">{{ card.desc }}</p></div>
              </div>
            </div>
            <div class="tinder-buttons">
              <button @click="onUserSelection('hate')" class="hate">
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.9 21.9"
                     xmlns:xlink="http://www.w3.org/1999/xlink" enable-background="new 0 0 21.9 21.9">
                  <path
                    d="M14.1,11.3c-0.2-0.2-0.2-0.5,0-0.7l7.5-7.5c0.2-0.2,0.3-0.5,0.3-0.7s-0.1-0.5-0.3-0.7l-1.4-1.4C20,0.1,19.7,0,19.5,0  c-0.3,0-0.5,0.1-0.7,0.3l-7.5,7.5c-0.2,0.2-0.5,0.2-0.7,0L3.1,0.3C2.9,0.1,2.6,0,2.4,0S1.9,0.1,1.7,0.3L0.3,1.7C0.1,1.9,0,2.2,0,2.4  s0.1,0.5,0.3,0.7l7.5,7.5c0.2,0.2,0.2,0.5,0,0.7l-7.5,7.5C0.1,19,0,19.3,0,19.5s0.1,0.5,0.3,0.7l1.4,1.4c0.2,0.2,0.5,0.3,0.7,0.3  s0.5-0.1,0.7-0.3l7.5-7.5c0.2-0.2,0.5-0.2,0.7,0l7.5,7.5c0.2,0.2,0.5,0.3,0.7,0.3s0.5-0.1,0.7-0.3l1.4-1.4c0.2-0.2,0.3-0.5,0.3-0.7  s-0.1-0.5-0.3-0.7L14.1,11.3z"/>
                </svg>
              </button>
              <button @click="onUserSelection('love')" class="love">
                <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
	 viewBox="0 0 492.719 492.719" style="enable-background:new 0 0 492.719 492.719;" xml:space="preserve">
<g>
	<g id="Icons_18_">
		<path d="M492.719,166.008c0-73.486-59.573-133.056-133.059-133.056c-47.985,0-89.891,25.484-113.302,63.569
			c-23.408-38.085-65.332-63.569-113.316-63.569C59.556,32.952,0,92.522,0,166.008c0,40.009,17.729,75.803,45.671,100.178
			l188.545,188.553c3.22,3.22,7.587,5.029,12.142,5.029c4.555,0,8.922-1.809,12.142-5.029l188.545-188.553
			C474.988,241.811,492.719,206.017,492.719,166.008z"/>
	</g>
</g>
</svg>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import SimpleDataCounter from './SimpleDataCounter.vue'
  import { EventBus } from '../main'
  import DataMapper from '../utils/DataMapper'
  import Utils from '../Utils'

  let swing = require('swing')
  let responsesMapping = {
    pizzaPreferences: {
      love: 'des lves la trouvent exquise',
      hate: 'des lves pensent qu\'il s\'agit d\'une hrsie gustative'
    },
    roquefortPreferences: {
      love: 'des lves trouvent qu\'il s\'agit du meilleur fromage',
      hate: 'des lves trouvent qu\'il s\'agit d\'un lait moisi'
    },
    licoricePreferences: {
      love: 'des lves trouvent qu\'il s\'agit d\'un rgal pour le palais',
      hate: 'des lves trouvent qu\'il s\'agit d\'une invention du diable'
    }
  }

  export default {
    components: {SimpleDataCounter},
    name: 'tinder-game',
    props: {
      cards: {type: Array, required: true}
    },
    data () {
      return {
        hatedResponse: false,
        lovedResponse: false
      }
    },
    created () {
      this.swingStack = swing.Stack()
    },
    mounted () {
      this.initCards()
      EventBus.$on('cards-reseted', () => {
        console.log('CARDS RESETS')
        this.initCards()
      })
    },
    computed: {
      currentCard () {
        return this.cards[0]
      },
      answer () {
        return DataMapper.getAnswer(this.currentCard.id)
      },
      questionId () {
        return DataMapper.getAnswerId(this.answer)
      },
      percents () {
        let avg = DataMapper.getAverageForAnswer(this.answer)
        let word = Utils.capitalize(Utils.getFirstWordCamelCase(this.questionId))
        return {
          hate: avg[`hate${word}`],
          love: avg[`love${word}`]
        }
      },
      responses () {
        return responsesMapping[this.questionId]
      }
    },
    methods: {
      closeOverlay () {
        this.lovedResponse = false
        this.hatedResponse = false
        this.refreshCardsZIndex()
        EventBus.$emit('card-removed', this.currentCard)
      },
      initCards () {
        console.log('INIT CARDS')
        this.$el.querySelectorAll('.tinder-card').forEach((card, i) => {
          card.classList.remove('dragging', 'drag-end')
          if (this.swingStack.getCard(card)) this.swingStack.getCard(card).destroy()
          const c = this.swingStack.createCard(card)
          c.on('throwout', (e) => {
            if (e.throwDirection === swing.Direction.RIGHT) {
              this.match()
            } else if (e.throwDirection === swing.Direction.LEFT) {
              this.match(true)
            }
          })
          c.on('dragstart', (e) => {
            e.target.classList.remove('drag-end')
            e.target.classList.add('dragging')
          })
          c.on('dragend', (e) => {
            e.target.classList.remove('dragging')
            e.target.classList.add('drag-end')
          })
        })
      },
      refreshCardsZIndex () {
        this.$el.querySelectorAll('.tinder-card').forEach(card => {
          card.classList.remove('dragging', 'drag-end')
          card.removeAttribute('style')
        })
      },
      onUserSelection (selection) {
        let card = this.swingStack.getCard(this.$el.querySelector('.tinder-card.current'))
        if (selection === 'love') {
          card.throwOut(150, 100)
          this.match()
        } else if (selection === 'hate') {
          card.throwOut(-150, -100)
          this.match(true)
        }
      },
      match (haters = false) {
        if (!this.cards || this.cards.length === 0) return
        if (haters) {
          this.lovedResponse = false
          this.hatedResponse = true
        } else {
          this.lovedResponse = true
          this.hatedResponse = false
        }
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/styles/utils/variables";

  .tinder-game {
    transform: rotate(-4deg);
    font-family: 'Mister Sinatra Slim', sans-serif;
    text-transform: uppercase;
    font-size: 1.2rem;
    & .iphone {
      background: url('../assets/images/licorice_tinder/iphone_shadow.png') center center / cover;
      width: 414px;
      height: 843px;
      z-index: 1;
      & .iphone-content {
        padding: 103px 28px 80px 28px;
        height: 820px;
        position: relative;
        overflow: hidden;
        & .iphone-status-bar {
          display: flex;
          justify-content: center;
          background: darken(whitesmoke, 10%);
          padding: 5px;
        }
        & .tinder-app {
          display: flex;
          flex-direction: column;
          height: 100%;
          background: whitesmoke;
          overflow: hidden;
          position: relative;
          &:after {
            transition: all 0.3s;
            visibility: hidden;
            position: absolute;
            z-index: 9998;
            opacity: 0;
            content: '';
            width: 100%;
            height: 100%;
            background: black;
          }
          &.overlay-visible {
            & .app-bar {
              filter: blur(3px);
              transform: scale(1.05);
            }
            & .app-content {
              filter: blur(3px);
              transform: scale(1.05);
            }
            &:after {
              opacity: 0.7;
              visibility: visible;
            }
          }
          & .app-icon {
            user-select: none;
          }
          & .tinder-match-overlay {
            display: flex;
            padding: 20px;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            position: absolute;
            left: 0;
            top: 0;
            background: transparent;
            width: 100%;
            height: 100%;
            z-index: 9999;
            color: whitesmoke;
            & p {
              font-size: 2rem;
              text-align: center;
            }
            & .match-photo {
              border-radius: 100%;
              width: 60%;
              border: 6px solid #e5497d;
            }
            & button {
              transition: all 0.3s;
              outline: none;
              color: whitesmoke;
              background: transparent;
              border-radius: 4px;
              border: 3px solid whitesmoke;
              padding: 20px 60px 14px;
              box-shadow: $tinder-shadow;
              font-family: 'Mister Sinatra Slim', sans-serif;
              font-size: 1.5rem;
              text-transform: uppercase;
              &:hover {
                cursor: pointer;
                background: lighten(#e5497d, 5%);
                background: whitesmoke;
                color: black;
              }
            }
          }
          & .app-bar {
            transition: filter 0.3s, transform 0.5s ;
            padding: 20px;
            text-align: center;
            & img {
              width: 150px;
            }
          }
          & .app-content {
            transition: filter 0.3s, transform 0.5s ;
            padding: 0 20px 20px 20px;
            display: flex;
            flex-direction: column;
            flex: 1;
            position: relative;
            & .tinder-cards {
              position: relative;
              & .tinder-card {
                display: flex;
                position: relative;
                z-index: 2;
                border-radius: 10px;
                overflow: hidden;
                flex-direction: column;
                box-shadow: $tinder-shadow;
                user-select: none;
                & img {
                  height: 340px;
                  object-fit: contain;
                  background: whitesmoke;
                  padding: 20px;
                  user-select: none;
                }
                & .informations {
                  text-align: left;
                  padding: 20px;
                  height: 80px;
                  background: #efefef;
                  z-index: 2;
                  & p {
                    font-size: 1rem;
                    letter-spacing: 1px;
                    margin-top: 5px;
                  }
                }
                &:first-of-type {
                  z-index: 10;
                }
                &:nth-of-type(2) {
                  z-index: 9;
                  transform: scale(0.9);
                  bottom: -7%;
                  position: absolute;
                  left: 0;
                }
                &:nth-of-type(3) {
                  z-index: 8;
                  position: absolute;
                  bottom: 0;
                  left: 0;
                }

                &.dragging {
                  z-index: 1000;
                }
                &.drag-end {
                  z-index: 1000;
                  &.current {
                    bottom: 0;
                  }
                }
              }
            }
            & .tinder-buttons {
              display: flex;
              justify-content: space-around;
              flex: 1;
              justify-items: center;
              align-items: center;
              padding-top: 30px;
              margin: 0 50px;
              & button {
                transition: all 0.3s;
                width: 70px;
                height: 70px;
                padding: 20px;
                border-radius: 100%;
                outline: none;
                border: 4px solid rgba(0, 0, 0, 0.08);
                background: whitesmoke;
                & svg {
                  transition: all 0.3s;
                }
                &:hover {
                  cursor: pointer;
                }
                &:active {
                  transform: scale(1.15);
                }
                &.hate {
                  border-color: $red;
                  svg {
                    fill: $red;
                  }
                  &:hover {
                    background: darken($red, 10%);
                    & svg {
                      fill: lighten($red, 10%);
                    }
                  }
                }
                &.love {
                  border-color: $green;
                  svg {
                    fill: $green;
                  }
                  &:hover {
                    background: darken($green, 10%);
                    & svg {
                      fill: lighten($green, 10%);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>



