import { countBy } from 'lodash'
import data from '../data/main.json'

export default class DataMapper {
  static getData () {
    let result = {}
    let sample = data[0]
    for (let questionKey in sample) {
      result[questionKey] = countBy(data, questionKey)
    }
    return result
  }
  static getAnswer (questionKey) {
    return this.getData()[questionKey]
  }

  static getAnswerId (answer) {
    let sample = data[0]
    let responsesId = Object.keys(answer)
    for (let questionId in sample) {
      if (sample.hasOwnProperty(questionId) && responsesId.join(' ').includes(sample[questionId])) {
        return questionId
      }
    }
    return null
  }

  static getAverageFor (questionKey) {
    let question = this.getData()[questionKey]
    let result = {}
    let total = this.getStudentsCount()
    for (let response in question) {
      if (question.hasOwnProperty(response)) {
        result[response] = Math.round((question[response] / total) * 100)
      }
    }
    return result
  }

  static getAverageForAnswer (answer) {
    let result = {}
    let total = this.getStudentsCount()
    for (let response in answer) {
      if (answer.hasOwnProperty(response)) {
        result[response] = Math.round((answer[response] / total) * 100)
      }
    }
    return result
  }

  static getStudentsCount () {
    return data.length
  }
}



