<template>
    <div class="addition">
      <img class="pin" src="../assets/images/pin.png" alt="">
      <h2 class="title">La citation qui rsume votre alimentation</h2>
      <div class="timestamp">{{ (new Date()).toLocaleTimeString("fr", { year: 'numeric', month: 'numeric', day: 'numeric' }) }}</div>
      <div class="responses" v-for="key in Object.keys(responses)">
        <div class="responses-count">{{ responses[key].length }}</div>
        <ul class="responses-list">
          <li v-for="response in responses[key]">{{ response }}</li>
        </ul>
        <div class="responses-percentage">
          <h3>{{ getPercentForQuote(key) }}%</h3>
        </div>
      </div>
      <div class="total">
        <h2>Total : {{ total }}%</h2>
      </div>
      <div class="informations">
        <ul>
          <li><h3>Aucun remboursement</h3></li>
          <li><h3>Aucun change</h3></li>
          <li><h3>Aucun retour</h3></li>
        </ul>
      </div>
    </div>
</template>

<script>
  import DataMapper from '../utils/DataMapper'

  let quoteMapper = {
    liveFromGrecAndWater: 'Je vis de grecs et d\'eau frache',
    junkForLife: 'Le gras c\'est la vie',
    foodSurcoted: 'La bouffe c\'est surcot',
    healthyForLife: '5 fruits et lgumes par jour'
  }

  export default {
    name: 'addition',
    computed: {
      responses () {
        let final = {}
        let answer = DataMapper.getAnswer('alimentationQuote')
        for (let responseId in answer) {
          if (answer.hasOwnProperty(responseId)) {
            final[responseId] = this.getResponsesFromQuote(answer, responseId)
          }
        }
        return final
      },
      total () {
        let answer = DataMapper.getAnswer('alimentationQuote')
        let total = 0
        for (let responseId in answer) {
          if (answer.hasOwnProperty(responseId)) {
            total += this.getResponsesFromQuote(answer, responseId).length
          }
        }
        return Math.floor((total / DataMapper.getStudentsCount()) * 100)
      }
    },
    mounted () {
    },
    methods: {
      getPercentForQuote (quoteId) {
        let total = DataMapper.getStudentsCount()
        let answer = DataMapper.getAnswer('alimentationQuote')
        let responses = this.getResponsesFromQuote(answer, quoteId)
        return parseFloat((responses.length / total) * 100).toFixed(2)
      },
      getResponsesFromQuote (answer, responseId) {
        let nb = answer[responseId]
        let responses = []
        for (let i = 0; i < nb; i++) {
          responses.push(quoteMapper[responseId])
        }
        return responses
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/styles/utils/variables";

  .addition {
    box-shadow: $shadow;
    margin-left: 160px;
    transform: rotate(5deg);
    position: relative;
    clip-path: polygon(1% 0.5%, 88% 0%, 91% 0%, 98% 1%, 100% 13%, 100% 17%, 100% 20%, 100% 26%, 100% 34%, 100% 43%, 100% 49%, 100% 57%, 100% 63%, 99% 69%, 100% 84%, 100% 93%, 100% 99%, 98% 100%, 92% 100%, 84% 100%, 73% 100%, 62% 100%, 56% 100%, 45% 100%, 38% 100%, 24% 99%, 16% 100%, 8% 100%, 1% 100%, 0% 91%, 1% 84%, 0% 76%, 0% 67%, 0% 51%, 0% 41%, 2% 2%, 5% 8%, 3% 7%, 0% 3%);
    filter: drop-shadow($shadow);
    left: 100px;
    background: url('../assets/textures/scratched_paper.jpg') top left / cover;
    display: flex;
    flex-direction: column;
    padding: 10px;
    font-family: 'Telidon Ink Hv';
    letter-spacing: 1px;
    overflow: visible;
    width: 430px;
    z-index: 99;
    & .pin {
      position: absolute;
      z-index: 100;
      width: 40px;
      right: 20px;
    }
    & .title {
      text-align: center;
      width: 100%;
      text-transform: uppercase;
      padding: 60px 40px;
    }
    & .informations {
      text-transform: uppercase;
      padding: 60px 40px;
      text-align: center;
      & li {
        margin-bottom: 10px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
    & .timestamp {
      text-align: right;
      margin-right: 20px;
    }
    & .total {
      padding: 20px;
      text-align: right;
    }
    & .responses {
      display: flex;
      justify-content: space-around;
      text-align: left;
      margin-top: 40px;
      border-bottom: 2px dotted $main-font-color;
      & .responses-count {
        margin-right: 20px;
      }
      & .responses-percentage {
        margin-bottom: 10px;
        align-self: flex-end;
        margin-right: 20px;
      }
      & .responses-list {
        flex: 1;
        padding-bottom: 40px;
      }
    }
  }
</style>



