<template>
  <div class="game-scratcher" :style="style">
    <canvas width="383" height="498" ref="scratcherCanvas"></canvas>
    <transition name="fade">
      <div class="results" v-if="finished">
        <simple-data-counter :use-currency-font="useCurrencyFont" :use-sinatra-font="useSinatraFont" size="medium" :percent="percent"></simple-data-counter>
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
//  import scratchSound from '../assets/sounds/scratch.wav'
  import SimpleDataCounter from './SimpleDataCounter.vue'

  export default {
    components: {SimpleDataCounter},
    name: 'GameScratcher',
    data () {
      return {
        finished: false
      }
    },
    props: {
      percent: {type: Number, default: 50},
      topImg: {type: String, required: true},
      bottomImg: {type: String, required: true},
      background: {type: String, required: true},
      useCurrencyFont: {type: Boolean, default: false},
      useSinatraFont: {type: Boolean, default: false},
      scratchAmount: {type: Number, default: 300},
      rotation: {type: Number, default: 0}
    },
    computed: {
      style () {
        return {
          backgroundImage: `url(${this.background})`,
          transform: `rotate(${this.rotation}deg) scale(0.9)`
        }
      }
    },
    mounted () {
      this.canvas = this.$refs.scratcherCanvas
//      this.audio = new Audio(scratchSound)
//      this.audio.loop = true
      this.scratched = 0
      this.bottomLayer = document.createElement('canvas')
      this.bottomLayer.width = this.canvas.width
      this.bottomLayer.height = this.canvas.height
      this.$el.insertBefore(this.bottomLayer, this.$el.firstChild)
      this.lastX = 0
      this.lastY = 0
      this.isDrawing = false
      this.ctx = this.canvas.getContext('2d')
      this.bottomLayerCtx = this.bottomLayer.getContext('2d')
      let top = document.createElement('img')
      top.src = this.topImg
      top.onload = () => {
        this.ctx.drawImage(top, 0, 0, top.width, top.height)
      }
      let bottom = document.createElement('img')
      bottom.src = this.bottomImg
      bottom.onload = () => {
        this.bottomLayerCtx.drawImage(bottom, 0, 0, bottom.width, bottom.height)
      }
      this.$once('finish', () => {
        this.finished = true
      })
      this.registerEventsListener()
    },
    methods: {
      registerEventsListener () {
        this.canvas.addEventListener('mouseup', this.onMouseUp.bind(this))
        this.canvas.addEventListener('mouseenter', this.onMouseEnter.bind(this))
        this.canvas.addEventListener('mousedown', this.onMouseDown.bind(this))
        this.canvas.addEventListener('mousemove', this.draw.bind(this))
        this.canvas.addEventListener('mouseout', this.onMouseUp.bind(this))
      },
      removeEventsListener () {
        this.canvas.removeEventListener('mouseup', this.onMouseUp)
        this.canvas.removeEventListener('mousedown', this.onMouseDown)
        this.canvas.removeEventListener('mousemove', this.draw)
        this.canvas.removeEventListener('mouseout', this.onMouseUp)
      },
      onMouseEnter (e) {
        if (e.buttons === 1) {
//          this.audio.play()
          this.isDrawing = true
        } else {
          this.isDrawing = false
//          this.audio.pause()
        }
      },
      onMouseUp () {
        this.isDrawing = false
//        this.audio.pause()
      },
      onMouseDown (e) {
//        this.audio.play()
        this.isDrawing = true
        this.lastX = e.offsetX
        this.lastY = e.offsetY
      },
      draw (e) {
        if (!this.isDrawing) return
        this.ctx.lineWidth = 26
        this.scratched += 1
        this.ctx.globalCompositeOperation = 'destination-out'
        this.bottomLayerCtx.globalCompositeOperation = 'source-over'
        this.ctx.beginPath()
        this.ctx.moveTo(this.lastX, this.lastY)
        this.ctx.lineTo(e.offsetX, e.offsetY)
        this.ctx.stroke()
        this.lastX = e.offsetX
        this.lastY = e.offsetY
        if (this.scratched > this.scratchAmount) {
          this.$emit('finish')
          this.removeEventsListener()
        }
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/styles/utils/variables";

  .game-scratcher {
    user-select: none;
    transform: scale(0.9);
    background-size: contain;
    display: flex;
    box-shadow: $shadow;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 450px;
    height: 600px;
    & .simple-data-counter {
      margin-left: 10px;
    }
    & canvas {
      position: absolute;
      transform: scale(0.8);
      top: 80px;
      &:active {
        cursor: url('../assets/images/5cents.png') 4 12, auto;
      }
    }
    & .results {
      position: absolute;
      left: 20px;
      bottom: 22px;
    }
  }
</style>



