<template>
    <div class="licorice-game-container">

      <div draggable="false" class="licorice-icon" :style="style" @click.stop="onMouseClick" @mousedown="onMouseDown" @mouseenter="onMouseEnter"  @mouseup.stop="onMouseUp" @mousemove.stop.self="onMouseMove">
        <div class="love">
          <transition name="fadeUp" mode="out-in">
            <p v-if="finished" style="font-size: 2rem;">Salut, je suis un composant oubli</p>
          </transition>
        </div>
        <div class="hate">
          <transition name="fadeDown" mode="out-in">
            <p v-if="finished" style="font-size: 2rem;">Hlas, mon crateur m'a fait mais je serai jamais utilis</p>
          </transition>
        </div>
      </div>
    </div>
</template>

<script>
  import SimpleDataCounter from './SimpleDataCounter.vue'
  import DataMapper from '../utils/DataMapper'
  let offset = 300
  let frames = 7
  let imageWidth = 1000
  export default {
    components: {SimpleDataCounter},
    props: {
      useCurrencyFont: {type: Boolean, default: false},
      useSinatraFont: {type: Boolean, default: false},
      answer: {type: Object, required: true}
    },
    name: 'licorice-game',
    data () {
      return {
        currentImage: 1,
        isDrawing: false
      }
    },
    computed: {
      finished () {
        return this.currentImage === frames
      },
      style () {
        return {
          backgroundPositionX: `${-((this.currentImage - 1) * imageWidth)}px`
        }
      },
      percents () {
        return DataMapper.getAverageForAnswer(this.answer)
      }
    },
    methods: {
      onMouseDown () {
        this.isDrawing = true
      },
      onMouseClick (e) {
        this.computeCurrentImage(e)
      },
      onMouseMove (e) {
        if (!this.isDrawing) return
        this.computeCurrentImage(e)
      },
      onMouseUp () {
        this.isDrawing = false
      },
      onMouseEnter (e) {
        this.isDrawing = e.buttons === 1
      },
      computeCurrentImage (e) {
        let result = e.offsetX - offset < 0 ? 0 : (e.offsetX - offset) / 1000
        this.currentImage = Math.floor(result * 10) + 1
      }
    }
  }
</script>

<style lang="scss">
  .licorice-game-container {
    width: 100%;
    overflow: hidden;
    position: relative;
    user-select: none;
    & .love, .hate {
      position: absolute;
      right: 0;
      text-align: right;
    }
    & .love {
      top: 235px;
    }
    & .hate {
      bottom: 20px;
    }
    & .licorice-icon {
      background: url('../assets/images/licoriceTilesheet.png');
      width: 1000px;
      height: 450px;
      margin: 0 auto;
      position: relative;
    }
  }
</style>



