<template>
  <div class="simple-data-counter" :class="inlineClass">
    <span ref="percent" :class="classString">{{ percent }}</span><span :class="'percentage ' + size">%</span>
    <span class="explanation"><slot></slot></span>
  </div>
</template>

<script>
  import Utils from '../Utils'

  export default {
    name: 'simple-data-counter',
    props: {
      percent: {type: Number, required: true},
      useCurrencyFont: {type: Boolean, default: false},
      useSinatraFont: {type: Boolean, default: false},
      displayInline: {type: Boolean, default: false},
      size: {type: String, default: 'medium'},
      duration: {type: Number, default: 3}
    },
    mounted () {
      let options = {
        root: null,
        threshold: 0.2
      }
      this.observer = new IntersectionObserver(this.observerCallback.bind(this), options)
      this.observer.observe(this.$el)
    },
    methods: {
      observerCallback (entries, observer) {
        entries.forEach(entry => {
          if (entry.intersectionRatio > 0) {
            Utils.animateValue(this.$refs.percent, 0, this.percent, 0, this.duration * 2)
            observer.unobserve(this.$el)
          }
        })
      }
    },
    data () {
      return {}
    },
    computed: {
      inlineClass () {
        if (!this.displayInline) return
        return `inline ${this.size}`
      },
      classString () {
        let str = `percent ${this.size}`
        if (this.useCurrencyFont || this.useSinatraFont) {
          str += ' custom-font'
          if (this.useCurrencyFont) str += ' currency'
          if (this.useSinatraFont) str += ' sinatra'
        }
        return str
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/styles/utils/variables";

  .simple-data-counter {
    &.inline {
      display: inline-block;
      &.small {
        width: 50px;
      }
      &.medium {
        width: 90px;
      }
      &.large {
        width: 212px;
      }
      &.inherit {
        width: 70px;
      }
      &.inherit {
        & .percent {
          &.small {
            font-size: inherit;
          }
          &.medium {
            font-size: inherit;
          }
          &.large {
            font-size: inherit;
          }
        }
      }
    }
    & .explanation {
      font-size: 1.6rem;
      font-family: $font-family;
    }
    & .percent {
      &.custom-font {
        &.currency {
          font-family: 'Currency';
          letter-spacing: 0.2rem;
        }
        &.sinatra {
          font-family: 'Mister Sinatra Slim';
        }
      }
      &.small {
        font-size: 2rem;
      }
      &.medium {
        font-size: 4rem;
      }
      &.large {
        font-size: 8rem;
      }
    }
    & .percentage {
      &.small {
        font-size: 1rem;
      }
      &.medium {
        font-size: 2rem;
      }
      &.large {
        font-size: 5rem;
      }
    }
  }
</style>



