<template>
  <div class="pizza-chart" @mousemove="onMouseEnter" @mouseenter="onMouseEnter" @mouseout="onMouseOut">
    <transition name="fade" mode="out-in">
      <div class="counters" v-if="hovered">
        <simple-data-counter :use-currency-font="useCurrencyFont" :use-sinatra-font="useSinatraFont" style="position: absolute; bottom: 0; right: -40px;" class="hate" :percent="percents.deliciousPizza"> adorent la pizza  l'ananas
        </simple-data-counter>
        <simple-data-counter :use-currency-font="useCurrencyFont" :use-sinatra-font="useSinatraFont" style="position: absolute; top: 0; left: -40px;" class="love" :percent="percents.heresyPizza"> dtesent la pizza  l'ananas
        </simple-data-counter>
      </div>
    </transition>
    <img id="first" draggable="false" src="../assets/images/pizzaFirstPart.png" alt="">
    <img id="second" draggable="false" src="../assets/images/pizzaSecondPart.png" alt="">
  </div>
</template>

<script>
  import DataMapper from '../utils/DataMapper'
  import SimpleDataCounter from './SimpleDataCounter.vue'

  export default {
    components: {SimpleDataCounter},
    name: 'pizza-chart',
    props: {
      useCurrencyFont: {type: Boolean, default: false},
      useSinatraFont: {type: Boolean, default: false},
      answer: {type: Object, required: true}
    },
    mounted () {
    },
    data () {
      return {
        hovered: false
      }
    },
    computed: {
      percents () {
        return DataMapper.getAverageForAnswer(this.answer)
      }
    },
    methods: {
      onMouseEnter () {
        this.$el.classList.add('hovered')
        this.hovered = true
      },
      onMouseOut () {
        this.$el.classList.remove('hovered')
        this.hovered = false
      }
    }
  }
</script>

<style lang="scss">
  .pizza-chart {
    position: relative;
    user-select: none;
    height: 768px;
    width: 768px;
    margin-top: 280px;
    & img {
      position: absolute;
      height: 100%;
      top: 0;
      left: 0;
      transition: all 0.2s cubic-bezier(0.47, -0.37, 0.54, 1.38);
    }
    &.hovered {
      & .counters {
        & .love {
          position: absolute;
          top: 0;
          left: -40px;
        }
        & .hate {
          position: absolute;
          bottom: 0;
          right: -40px;
          text-align: right;
        }
      }
      img {
        transform: scale3d(0.8, 0.8, 0.8) rotate(45deg);
        /*filter: blur(2px)*/
      }
      & #first {
        top: 50px;
        left: -150px;
      }
      & #second {
        top: -50px;
        left: 50px;
      }
    }
  }

</style>



