let CountUp = require('countup.js')

export default class Utils {
  static animateValue (elem, start, end, decimals, duration) {
    let countUp = new CountUp(elem, start, end, decimals, duration)
    countUp.start()
  }
  static capitalize (string) {
    return string.charAt(0).toUpperCase() + string.slice(1)
  }
  static randomBetween (min, max) {
    return Math.floor(Math.random() * max) + min
  }
  static getCoords (elem) { // crossbrowser version
    let box = elem.getBoundingClientRect()

    let body = document.body
    let docEl = document.documentElement

    let scrollTop = window.pageYOffset || docEl.scrollTop || body.scrollTop
    let scrollLeft = window.pageXOffset || docEl.scrollLeft || body.scrollLeft

    let clientTop = docEl.clientTop || body.clientTop || 0
    let clientLeft = docEl.clientLeft || body.clientLeft || 0

    let top = box.top + scrollTop - clientTop
    let left = box.left + scrollLeft - clientLeft

    return { top: Math.round(top), left: Math.round(left) }
  }

  static getFirstWordCamelCase (camelCaseString) {
    return camelCaseString.split(/(?=[A-Z])/).map(s => s.toLowerCase())[0]
  }
}



